'use client';

import Link from 'next/link';
import { prisma } from '@/lib/prisma';
import { Search, ChevronRight, Filter, Download } from 'lucide-react';
import { Button, Badge, Card, Input, Select } from '@/components/ui';

export default async function ScansPage() {
    const scans = await prisma.scan.findMany({
        orderBy: { createdAt: 'desc' },
    });

    return (
        <div className="max-w-7xl mx-auto space-y-8">
            {/* Page Header */}
            <div className="flex flex-col md:flex-row justify-between items-start md:items-end gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Rank Tracker</h1>
                    <p className="text-gray-500 mt-1">Manage all your local grid tracking campaigns.</p>
                </div>
                <div className="flex gap-3">
                    <Button variant="outline" className="hidden md:flex">
                        <Download className="mr-2 w-4 h-4" /> Export All
                    </Button>
                    <Link href="/scans/new">
                        <Button>New Ranking Report</Button>
                    </Link>
                </div>
            </div>

            {/* Filters & Search */}
            <Card className="p-4 flex flex-col md:flex-row gap-4">
                <div className="flex-1">
                    <Input
                        placeholder="Search keywords or locations..."
                        icon={<Search size={16} />}
                    />
                </div>
                <div className="w-full md:w-48">
                    <Select icon={<Filter size={16} />}>
                        <option value="all">All Statuses</option>
                        <option value="running">Running</option>
                        <option value="completed">Completed</option>
                    </Select>
                </div>
            </Card>

            {/* Data Table */}
            <Card noPadding className="overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-gray-50 border-b border-gray-100">
                        <tr>
                            <th className="px-6 py-4 font-semibold text-gray-500 text-xs uppercase tracking-wider">Keyword / Date</th>
                            <th className="px-6 py-4 font-semibold text-gray-500 text-xs uppercase tracking-wider">Location</th>
                            <th className="px-6 py-4 font-semibold text-gray-500 text-xs uppercase tracking-wider">Config</th>
                            <th className="px-6 py-4 font-semibold text-gray-500 text-xs uppercase tracking-wider">Frequency</th>
                            <th className="px-6 py-4 font-semibold text-gray-500 text-xs uppercase tracking-wider">Status</th>
                            <th className="px-6 py-4 font-semibold text-gray-500 text-xs uppercase tracking-wider text-right">Action</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-100 bg-white">
                        {scans.length === 0 ? (
                            <tr>
                                <td colSpan={6} className="px-6 py-16 text-center text-gray-400">
                                    <div className="flex flex-col items-center">
                                        <Search size={48} className="mb-4 opacity-20" />
                                        <p className="text-lg font-medium text-gray-900">No tracking reports found</p>
                                        <p className="text-sm mt-1 mb-6">Create your first campaign to start tracking local rankings.</p>
                                        <Link href="/scans/new">
                                            <Button>Create First Tracker</Button>
                                        </Link>
                                    </div>
                                </td>
                            </tr>
                        ) : (
                            scans.map((scan) => (
                                <tr key={scan.id} className="hover:bg-gray-50 transition-colors group">
                                    <td className="px-6 py-4">
                                        <div className="font-semibold text-gray-900">{scan.keyword}</div>
                                        <div className="text-xs text-gray-500 mt-0.5">{new Date(scan.createdAt).toLocaleDateString()}</div>
                                    </td>
                                    <td className="px-6 py-4 text-sm text-gray-600">
                                        {scan.centerLat.toFixed(3)}, {scan.centerLng.toFixed(3)}
                                    </td>
                                    <td className="px-6 py-4 text-sm text-gray-600">
                                        {scan.gridSize}x{scan.gridSize} <span className="text-gray-400">({scan.radius}km)</span>
                                    </td>
                                    <td className="px-6 py-4">
                                        <span className="inline-flex items-center px-2 py-1 rounded text-xs font-medium bg-gray-100 text-gray-600 uppercase">
                                            {scan.frequency}
                                        </span>
                                    </td>
                                    <td className="px-6 py-4">
                                        <Badge variant={
                                            scan.status === 'COMPLETED' ? 'success' :
                                                scan.status === 'RUNNING' ? 'blue' :
                                                    'default'
                                        }>
                                            {scan.status}
                                        </Badge>
                                    </td>
                                    <td className="px-6 py-4 text-right">
                                        <Link href={`/scans/${scan.id}`} className="inline-flex items-center text-gray-400 hover:text-blue-600 transition-colors">
                                            <span className="text-sm mr-2 opacity-0 group-hover:opacity-100 transition-opacity font-medium">View Report</span>
                                            <ChevronRight size={18} />
                                        </Link>
                                    </td>
                                </tr>
                            ))
                        )}
                    </tbody>
                </table>
            </Card>
        </div>
    );
}
